<template>
  <div class="content">
    <div class="box">
      <div class="point">
        <img src="../assets/btn.png" alt="" :class="['img', active == true ? 'turn' : '']" @click="choujiang" v-show="show==false" />
        <img src="../assets/btn.png" alt="" :class="['img', showActive == true ? 'slowTurn' : '']" v-show="show==true && show1==false"  />
        <img src="../assets/btn.png" alt="" :class="['img', showActive1 == true ? 'slowTurn1' : '']" v-show="show1==true"  />
      </div>
    </div>
    <van-popup v-model="popup" position="bottom" :style="{ height: '70%' }" round >
      <div class="tit desc">恭喜你获得一等奖!</div>
      <div class="tit">请马上联系工作人员兑换奖品。</div>
      <div class="imgBox"><img src="../assets/girl.jpg" alt="" /></div>
    </van-popup>
  </div>
</template>
<style scoped>
    img{margin:0;display:block;}
    p{margin:0;padding:0}
    .content{width:100%;height:100vh;overflow:hidden;}
    .box{background:url('../assets/choujiang.png')no-repeat;background-position:center;background-size:100%;width:100%;height:100%;position:relative;}
    .point{position:absolute;top:36%;left:35%;}
    .point img{width:100%;height:auto;display:block;}
    .turn{animation:turn .3s linear infinite;}
    @keyframes turn{
        0%{-webkit-transform:rotate(0deg);}
        100%{-webkit-transform:rotate(360deg);}
    }
    .slowTurn{animation:turn .5s linear infinite;}
    
    .slowTurn1{animation:turn 2s linear infinite;}

    .tit{margin-top:30px;text-align:center;font-size:18px;}
    .imgBox{width:90%;margin:30px 5%;border-radius:15px;overflow:hidden;}
    .imgBox>img{width:100%;height:auto}
    .desc{color:#FF8811;font-weight:700}
    
</style>
<script>
// @ is an alias to /src

export default {
  data() {
    return {
      active: false,
      show: false,
      show1: false,
      showActive: false,
      showActive1: false,
      popup: false,
      token: true
    }
  },
  mounted(){
    // console.log(window.localStorage.getItem('token'))
    if(window.localStorage.getItem('token') == 'true'){
      this.showPopup();
    }
  },
  methods: {
    choujiang(){
      let that = this;
      that.active = true;
      setTimeout(() => {
        // console.log('恭喜您获得一等奖，请联系13510109519兑换')
        that.active = false
        that.show = true
        that.showActive = true
      }, 2000);
      setTimeout(() => {
        that.showActive = false
        that.showActive1 = true
      }, 4000);
      setTimeout(() => {
        that.show1 = true
        that.showActive1 = false
        this.showPopup()
      }, 4500);
    },
    showPopup() {
      this.popup = true;
      window.localStorage.setItem('token', true)
    },
  }
}
</script>
